html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--verydblue);
  color: var(--white);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.375rem;
}
@media (min-width: 71.875em) {
  h2 {
    font-size: 1.5rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --bgblue: hsl(210, 78%, 56%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-mid: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --verydblue: hsl(230, 17%, 14%);
  --desatdbblue: hsl(228, 28%, 20%);
  --desatblue: hsl(228, 38%, 66%);
  --white: hsl(0, 0%, 100%);
  --paleblue: hsl(225, 100%, 98%);
  --lgrayishblue: hsl(227, 47%, 96%);
  --dgrayishblue: hsl(228, 12%, 44%);
  --verydblue2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .card-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  padding: 2.0625rem;
  background: var(--desatdbblue);
  color: var(--desatblue);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  height: 0.3125rem;
  width: 100%;
  background: var(--instagram-start);
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-mid) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  margin-top: 0.3125rem;
  height: 1.25rem;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--white);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  color: var(--desatblue);
  letter-spacing: 0.3125rem;
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 0.5625rem;
}
.card__change {
  display: flex;
  font-size: 0.75rem;
  font-weight: 700;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}
.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.75rem;
  border: none;
  gap: 0.3rem 0.5rem;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem 0 -0.125rem;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid darkslategray;
}
.toggle__background {
  border-radius: 0.75rem;
  overflow: hidden;
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  background: linear-gradient(225deg, var(--limegreen) 0%, var(--bgblue) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--verydblue);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--desatblue);
}